.hero_img{
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    height:60vh;
    position: relative;
}
.hero_img::before{
    content: "";
    background: url('https://images.unsplash.com/photo-1611924707078-da8777fc99cb?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Nnx8Y29tcHV0ZXIlMjBzdHVkeXxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=700&q=60');
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero_img h1{
    font-size: 2.4rem;
}
.hero_img p{
    font-size: 1.4rem;
    text-align: center;
}
@media screen and (max-width:640px){
    .hero_img h1{
        font-size: 2rem;
    }   
}