.about{
    width:100%;
    margin: 3rem 0;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    align-items: center;
    justify-content: center;
}

.about .left{
    text-align: center;
    margin: auto;
    padding: 1rem;
    width: 48%;
    text-align: center;
    justify-content: center;

}
.about .left p{
    margin:1.2rem 0;
}
 .right{
   max-width: 48%;
}
.img_container{
    display: grid;
    grid-template-columns: repeat(12,1fr);
    position: relative;
    align-items: center;
    text-align: center;
}
.right .img{
    max-width: 90%;
    border: 1px solid #333;
}

.top{
    grid-column: 1/span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}
.bottom{
    grid-column: 4/-1;
    grid-row: 1;
    z-index: 5;
}

@media screen and (max-width:640px) {
    .about{
        grid-template-columns: repeat(1,1fr);
        grid-template-rows: auto;
    }
}